@import "main";
// End Of Import

// Loader
.loader {
    @extend .Flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999999;

    img {
        width: 250px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(#264f78, 0.95);
    z-index: 9;
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: #264f78;
    z-index: 9999;
    overflow-y: auto;
    transition: 0.5s;
    @extend .Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);

    > img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.5);
        opacity: 0.3;
    }

    html[dir="ltr"] & {
        transform-origin: left;
    }

    &.open {
        // transform-origin: left;
        transform: scaleX(1);

        ul {
            opacity: 1;

            li {
                transform: translateX(0);
                opacity: 1;
            }
        }
    }

    &.origin {
        transform-origin: left;

        html[dir="ltr"] & {
            transform-origin: right;
        }
    }

    .options {
        padding: 0 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 50px;
        direction: ltr;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 14px;
            color: $color-sec;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: #fff;
        }

        .close1 {
            display: flex;
            cursor: pointer;

            img {
                filter: brightness(0) invert(1);
            }
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        opacity: 0;
        transition: 0.5s;
        transition-delay: 0.5s;

        & > li {
            position: relative;
            padding: 10px 0;
            // margin-bottom: 20px;
            transition: 0.5s;
            transform: translateX(10px);
            opacity: 0;

            &:nth-child(1) {
                transition-delay: 0.3s;
            }
            &:nth-child(2) {
                transition-delay: 0.5s;
            }
            &:nth-child(3) {
                transition-delay: 0.7s;
            }
            &:nth-child(4) {
                transition-delay: 0.9s;
            }
            &:nth-child(5) {
                transition-delay: 1.1s;
            }
            &:nth-child(6) {
                transition-delay: 1.3s;
            }

            a {
                width: 100%;
                display: block;
                font-size: 20px;
                color: $color-white;
                transition: 0.5s;
            }

            i {
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: 0.5s;

                html[dir="ltr"] & {
                    left: unset;
                    right: 13px;
                }

                &.open {
                    transform: rotate(0deg);
                }
            }

            .drop {
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li {
                    padding: 4px 0;

                    a {
                        font-size: 15px;
                        color: #898989;
                        transition: 0.5s;

                        &:hover {
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Start Header
header {
    min-height: 100vh;
    position: relative;

    #particles-js {
        background-image: linear-gradient(270deg, #12405a 0%, #264f78 100%);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .TopNav {
        padding-top: 20px;
        padding-bottom: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        transition: 0.5s;
        z-index: 999;

        &.hide {
            transform: translateY(-100%);
        }

        &.back {
            position: fixed;
            transform: translateY(0);
            background-image: linear-gradient(270deg, #12405a 0%, #264f78 100%);
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
        }

        .content {
            display: flex;
            align-items: center;
        }

        // @include Sm {
        //     justify-content: space-between;
        // }
        .logo {
            height: 70px;
            @include Sm {
                margin-left: auto;
            }
        }

        .links {
            margin-right: 128px;
            margin-left: auto;
            display: flex;
            align-items: center;
            @include LTR {
                margin-left: 128px;
                margin-right: auto;
            }

            @include Sm {
                display: none;
            }

            li {
                &:not(:last-of-type) {
                    margin-left: 25px;
                    @include LTR {
                        margin-right: 25px;
                        margin-left: 0;
                    }
                }
                a {
                    transition: 0.5s;
                    font-size: 18px;
                    font-family: med;
                    color: transparent;
                    // background-image: linear-gradient(to right, #d1a42e 0%, #fff 100%);
                    background-image: linear-gradient(
                        to right,
                        #d1a42e,
                        #d1a42e 50%,
                        #fff 50%
                    );
                    background-size: 200% 100%;
                    background-position: 100%;
                    background-clip: text;
                    -webkit-background-clip: text;

                    &:hover {
                        background-position: 0%;
                    }
                }
            }
        }

        .teckit {
            width: 150px;
            height: 50px;
            background-color: $color-sec;
            border-radius: 100px;
            @extend .Flex;
            transition: 0.5s;
            @extend .hover;

            svg {
                margin-left: 10px;
                @include LTR {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }

            span {
                margin-top: 4px;
                font-size: 18px;
                font-family: med;
                color: #fff;
                text-decoration: none;
            }

            @include mob480 {
                width: 50px;
                height: 50px;

                span {
                    display: none;
                }

                svg {
                    margin: 0;
                }
            }
        }

        .menuTriger {
            display: none;
            justify-content: center;
            align-items: center;
            margin-right: 25px;

            @include Sm {
                display: flex;
            }

            img {
                width: 40px;
                filter: invert(85%) sepia(21%) saturate(1757%)
                    hue-rotate(340deg) brightness(89%) contrast(83%);
            }
        }
    }

    > .container {
        position: relative;
        min-height: 100vh;
    }

    .slider {
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .swiper-container {
            padding-bottom: 13px;
        }

        .swiper-pagination {
            top: 50%;
            right: -100px;
            transform: rotate(90deg) translateY(-50%);
            height: 0;

            @include Xs {
                top: unset;
                right: unset;
                bottom: -20px;
                left: 50%;
                transform: translateX(-50%);
            }

            span {
                width: 17px;
                height: 5px;
                border-radius: 3px;
                background-color: #fff;
                transition: 0.5s;
                margin: 0 2.5px;

                &:focus {
                    outline: none;
                }

                &.pagination-active-bullet {
                    opacity: 1;
                }
            }
        }

        .swiper-slide {
            text-align: right;
            @include LTR {
                text-align: left;
            }

            .row {
                @include mob480 {
                    margin: 0 !important;
                }
            }

            p {
                transform: translateX(-15px);
                opacity: 0;
                &:first-child {
                    font-size: 22px;
                    color: #fff;
                    margin-bottom: 19px;
                    transition: 0.7s 1s;
                }

                &:last-of-type {
                    font-size: 20px;
                    color: #fff;
                    line-height: 35px;
                    width: 450px;
                    margin-bottom: 36px;
                    transition: 0.7s 1.4s;

                    @include mob480 {
                        width: auto;
                    }
                }
            }

            h1 {
                transform: translateX(-15px);
                opacity: 0;
                font-size: 40px;
                font-family: bold;
                color: #fff;
                position: relative;
                margin-bottom: 42px;
                transition: 0.7s 1.2s;

                &:after {
                    content: "";
                    display: block;
                    width: 38px;
                    height: 3px;
                    background-color: $color-sec;
                    margin-top: 10px;
                }

                @include mob480 {
                    font-size: 30px;
                }
            }

            a {
                transform: translateX(-15px);
                opacity: 0;
                @extend .Flex;
                width: 130px;
                height: 50px;
                background-color: $color-sec;
                color: #fff;
                border-radius: 100px;
                font-size: 18px;
                font-family: med;
                @extend .hover;
                transition: 0.7s 1.6s;
                padding-top: 4px;
            }

            .inner-content {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            .slider-pic-animate {
                position: relative;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                padding-top: 70px;

                @media (min-width: 1200px) and (max-width: 1400px) {
                    margin-top: 100px;
                }

                @include Sm {
                    display: none;
                }

                .pos {
                    position: absolute;
                    // transition: transform 1s cubic-bezier(0.34, 1.56, 0.64, 1);
                    transition: 1s;

                    &:last-of-type {
                        top: 225px;
                        right: 180px;
                        transform: translateY(-15px);
                        transition-delay: 0.2s;
                    }
                    &:nth-child(2) {
                        transition-delay: 0.4s;
                        top: 55px;
                        right: 140px;
                        transform: translateY(-15px);
                    }
                    &:nth-child(3) {
                        transition-delay: 0.6s;
                        top: 20px;
                        right: 210px;
                        transform: translateY(-15px);
                    }
                    &:nth-child(4) {
                        transition-delay: 0.2s;
                        top: 135px;
                        right: 140px;
                        transform: translateY(-15px);
                    }
                }
            }

            &.swiper-slide-active {
                .pos {
                    transform: none !important;
                }

                .inner-content {
                    > * {
                        transform: none !important;
                        filter: none;
                        opacity: 1;
                    }
                    a {
                        filter: url("#goo");
                    }
                }
            }
        }
    }

    .social {
        position: absolute;
        bottom: 70px;
        right: 0;

        @include mob480 {
            // right: 50%;
            // transform: translateX(50%);
            transform: translateY(90vh);
            display: flex;
            justify-content: center;
            position: unset;
        }

        a {
            opacity: 0.3;
            &:not(:last-child) {
                margin-left: 7px;
                @include LTR {
                    margin-right: 7px;
                    margin-left: 0;
                }
            }

            &:hover {
                animation: flash 1s linear;
            }
        }
    }
}
// End Header

// OverView

.overview-sec {
    padding: 80px 0 0;
    text-align: center;

    .col-lg-4 {
        &:hover {
            .img {
                svg {
                    animation: anim 0.7s linear;
                }
            }

            h2 {
                color: $color-sec;
            }
        }
    }

    .img {
        width: 98.5px;
        height: 100px;
        margin: auto;
        position: relative;

        svg {
            position: absolute;
            transition: 0.5s;

            &:first-child {
                top: 0;
                left: 0;
                transform-origin: left bottom;
            }

            &:last-child {
                bottom: 0;
                right: 0;
                z-index: -1;
                transform-origin: left top;
            }
        }
    }

    h2 {
        transition: 0.5s;
        margin-top: 40px;
        margin-bottom: 19px;
        font-size: 25px;
        font-family: bold;
        color: #244d75;
    }

    p {
        font-size: 18px;
        font-family: med;
        color: #000000;
        line-height: 25px;
        margin-bottom: 0;
    }
}

// OverView

.about-section {
    padding: 80px 0 0;
    @include Sm {
        padding: 50px 0;
    }
    .about-right {
        color: #fff;
        background-color: #264f78;
        position: relative;
        border-radius: 0px 40px;
        overflow: hidden;
        @include Sm {
            margin-bottom: 30px;
        }

        & > svg {
            position: absolute;
            z-index: 1;
            display: block;
            width: 100%;
            height: 100%;
            transition: 0.5s;

            @include Sm {
                right: -20%;
            }

            @include Xs {
                right: -7%;
            }

            @include mob375 {
                transform: scale(1.15);
            }
        }
        &:hover > svg {
            transform: translateX(-5px) scale(1.05);
            filter: blur(5px);
        }
        .content {
            position: relative;
            padding: 90px 50px;

            @include Sm {
                text-align: center;
            }

            @include mob480 {
                padding: 50px 30px;
            }
            @include mob375 {
                padding: 30px 20px;
            }
            width: 100%;
            height: 100%;
            z-index: 2;
            .title {
                h2 {
                    font-family: "bold";
                    font-size: 40px;
                    margin-bottom: 18px;
                }
                p {
                    font-size: 18px;
                    padding-bottom: 20px;
                    margin-bottom: 28px;
                    font-family: "light";
                    position: relative;
                    &::before {
                        content: "";
                        width: 38px;
                        height: 3px;
                        border-radius: 4px;
                        background: #d1a42e 0% 0% no-repeat padding-box;
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        @include LTR {
                            right: unset;
                            left: 0;
                        }

                        @include Sm {
                            right: 50%;
                            transform: translateX(50%);
                        }
                    }
                }
            }
            p {
                font-family: "light";
                font-size: 16px;
                line-height: 2;
                margin-bottom: 20px;
            }
            a {
                padding-top: 4px;
                @extend .Flex;
                width: 130px;
                height: 50px;
                background-color: $color-sec;
                color: #fff;
                border-radius: 100px;
                font-size: 18px;
                font-family: med;
                @extend .hover;

                @include Sm {
                    margin-right: auto;
                    margin-left: auto;
                }
            }
        }
    }
    .about-left {
        border-radius: 40px 0px;
        box-shadow: 0px 0px 30px #244d7526;
        width: 100%;
        height: 100%;
        padding: 40px 50px;
        @include mob375 {
            padding: 20px;
        }

        .content-box {
            display: flex;
            align-items: center;
            &:not(:last-of-type) {
                margin-bottom: 20px;
            }
            @include Sm {
                text-align: center;
            }
            @include mob480 {
                flex-direction: column;
                align-items: unset;
            }

            .icon-box {
                width: 107px;
                height: 107px;
                border: 1px solid #244d75;
                border-radius: 30px 0px;
                margin-left: 30px;
                @include Sm {
                    margin-right: auto;
                    margin-left: auto;
                }
                @include mob480 {
                    margin-bottom: 30px;
                }

                @include LTR {
                    margin-left: unset;
                    margin-right: 30px;
                }
                background-image: linear-gradient(
                    to right,
                    #ffffff 50%,
                    #d1a42e,
                    #d1a42e 50%
                );
                background-size: 200% 100%;
                background-position: 0%;
                transition: 0.5s;
                @extend .Flex;
                path,
                circle {
                    transition: 0.5s;
                }
            }
            .content {
                flex: 1;
                h3 {
                    color: #244d75;
                    font-size: 25px;
                    font-family: "bold";
                    margin-bottom: 20px;
                    color: transparent;
                    background-image: linear-gradient(
                        to right,
                        #244d75 50%,
                        #d1a42e,
                        #d1a42e 50%
                    );
                    background-size: 200% 100%;
                    background-position: 0%;
                    background-clip: text;
                    transition: 0.5s;
                }
                p {
                    font-family: "med";
                    font-size: 16px;
                    margin-bottom: 0;
                }
            }
            &:hover {
                .icon-box {
                    border-color: #d1a42e;
                    background-position: 100%;

                    svg {
                        path,
                        circle {
                            fill: #fff;
                        }
                    }
                }
                .content {
                    h3 {
                        background-position: 100%;
                        transition-delay: 0.4s;
                    }
                }
            }
        }
    }
}
