.offers-section {
    background-image: url("../images/offers.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 80px 0;
    @include Sm {
        padding: 50px 0;
    }

    .title {
        text-align: center;
        color: #ffffff;
        font-family: "bold";
