            width: 100%;
            height: 100%;
            background-color: #244d75;
            position: absolute;
            top: 0;
            left: 100%;
            transform: skewX(25deg);
            transition: 0.5s;
        }

        &:hover {
            &:after {
                left: -20px;
            }

            h3 {
                color: #fff;
            }
        }

        .icon-box {
            width: 76px;
            height: 76px;
            background: #244d75 0% 0% no-repeat padding-box;
            border-radius: 0px 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            @include mob480 {
                height: 100%;
            }
            position: relative;
            z-index: 99;
        }
        h3 {
            margin-right: 20px;
            position: relative;
            z-index: 99;
            @include LTR {
                margin-left: 20px;
                margin-right: unset;
            }
            transition: 0.5s;
            margin-bottom: 0;
            font-size: 25px;
            font-family: "med";
            color: #000000;
            text-decoration: none;
            @include mob480 {
                font-size: 17px;
            }
            @include mob320 {
                font-size: 16px;
            }
        }
    }
}

// Our-work

.our-work {
    padding-bottom: 80px;
    padding-top: 80px;
    .head {
        position: relative;
        h1 {
            position: relative;
            font-size: 35px;
            font-family: bold;
            color: #244d75;
            text-align: center;

            @include Xs {
                text-align: initial;
            }

            &:after {
                content: "";
                display: block;
                background-color: #d1a42e;
                width: 38px;
                height: 3px;
                margin: 15px auto 60px;

                @include Xs {
                    margin: 15px 0 60px;
                }
            }
        }
        .more-work {
            position: absolute;
            top: 5px;
            left: 0;
            width: 130px;
            height: 50px;
            background-color: #244d75;
            border-radius: 100px;
            @extend .Flex;
            font-size: 18px;
            font-family: med;
            color: #fff;
            @extend .hover;
            margin-top: 0;
            padding-top: 4px;

            @include Xs {
                top: -7px;
            }

            &:after,
            &:before {
                background-color: #244d75;
            }
        }
    }

    .col-lg-6 {
        &:nth-child(1),
        &:nth-child(4) {
            .work,
            .overlay,
            .overlay1 {
                border-radius: 0 40px 0 40px;
            }
        }
        &:nth-child(2),
        &:nth-child(3) {
            .work,
            .overlay,
            .overlay1 {
                border-radius: 40px 0 40px 0;
            }
        }
    }

    .work {
        position: relative;
        overflow: hidden;
        margin-bottom: 30px;

        > img {
            width: 100%;
            object-fit: cover;
        }

        .overlay1 {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.6);
            z-index: 5;
            transition: 0.5s;
            opacity: 0;
            visibility: hidden;
        }

        .overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(
                to bottom,
                rgba(#244d75, 0),
                #244d75
            );
            padding: 0 25px;
            @extend .Flex;
            flex-direction: column;
            z-index: 9;
            transition: 0.5s;
            opacity: 0;
            visibility: hidden;

            > * {
                filter: blur(5px);
                transform: scale(1.5);
                opacity: 0;
                transition-duration: 0.5s;
                transition-delay: 0.2s;
            }

            h3 {
                font-size: 25px;
                font-family: bold;
                color: #fff;

                @include mob375 {
                    font-size: 20px;
                }
            }

            p {
                font-size: 16px;
                font-family: med;
                color: rgba(255, 255, 255, 0.6);
                margin: 19px 0 24px;
                text-align: center;
                line-height: 25px;

                @include mob375 {
                    margin: 5px 0 15px;
                    font-size: 15px;
                }
            }

            .action {
                @extend .Flex;
                a {
                    position: relative;
                    @extend .Flex;
                    width: 83px;
                    height: 70px;
                    background-color: $color-sec;
                    overflow: hidden;

                    @include mob375 {
                        width: 68px;
                        height: 55px;
                    }

                    &:first-child {
                        border-radius: 0 40px 0 40px;

                        @include mob375 {
                            border-radius: 0 30px 0 30px;
                        }
                        &:after {
                            border-radius: 0 40px 0 40px;

                            @include mob375 {
                                border-radius: 0 30px 0 30px;
                            }
                        }
                        margin-left: 20px;
                    }
                    &:last-child {
                        border-radius: 40px 0 40px 0;
                        @include mob375 {
                            border-radius: 30px 0 30px 0;
                        }
                        &:after {
                            border-radius: 40px 0 40px 0;
                            @include mob375 {
                                border-radius: 30px 0 30px 0;
                            }
                        }
                    }

                    &:after {
                        content: "";
                        width: 100%;
                        height: 100%;
                        background-color: #244d75;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%) scale(0);
                        transition: 0.5s;
                    }

                    &:hover {
                        &:after {
                            transform: translate(-50%, -50%) scale(0.9);
                        }

                        svg {
                            position: relative;
                            z-index: 9;
                        }
                    }
                }
            }
        }
        &:hover {
            .overlay1 {
                opacity: 1;
                visibility: visible;
            }
            .overlay {
                opacity: 1;
                visibility: visible;

                > * {
                    filter: none;
                    transform: none;
                    opacity: 1;
                }
            }
        }
    }

    .more-work {
        margin: 20px auto 0;
        width: 130px;
        height: 50px;
        background-color: #244d75;
        border-radius: 100px;
        @extend .Flex;
        font-size: 18px;
        font-family: med;
        color: #fff;
        @extend .hover;
        display: none;
        @include Sm {
            display: flex;
        }

        &:after,
        &:before {
            background-color: #244d75;
        }
    }
}

