  background-image: linear-gradient(to right, #d1a42e 50%, #244d75, #244d75 50%);
  background-size: 200% 100%;
  background-position: 0%;
  transition: 0.5s;
}

.contact-Min .cont a {
  font-size: 25px;
  font-family: med;
  color: #000;
  transition: 0.5s;
}

@media (max-width: 375px) {
  .contact-Min .cont a {
    font-size: 20px;
  }
}

.contact-Min .cont:hover .icon {
  border-color: #d1a42e;
  background-position: 100%;
}

.contact-Min .cont:hover .icon svg {
  transform: rotateY(360deg);
  transition: 0.5s;
}

.contact-Min .cont:hover a {
  color: #244d75;
}

.contact-Min .cont:last-child a {
  display: flex;
  flex-direction: column;
}

.contact-Min .cont:last-child a span:last-child {
  font-size: 18px;
  color: #ababab;
}

footer {
  padding-top: 80px;
}

footer .logo {
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

footer .links {
  flex-wrap: wrap;
  margin-bottom: 40px;
}

footer .links li {
  margin-bottom: 15px;
}

footer .links li:not(:last-child) {
  margin-left: 24px;
}

footer .links a {
  transition: 0.5s;
  font-size: 18px;
  font-family: med;
  color: transparent;
  background-image: linear-gradient(to right, #d1a42e, #d1a42e 50%, #000 50%);
  background-size: 200% 100%;
  background-position: 100%;
  background-clip: text;
  -webkit-background-clip: text;
}

footer .links a:hover {
  background-position: 0%;
}

footer .btns {
  flex-wrap: wrap;
  width: 665px;
  margin: auto;
}

@media (max-width: 767px) {
  footer .btns {
    width: 100%;
  }
}

footer .btns a {
  padding-top: 4px;
  position: relative;
  width: 125px;
  height: 40px;
  border: 1px solid #244d75;
  border-radius: 100px;
  font-size: 16px;
  font-family: med;
  color: #244d75;
  margin-bottom: 15px;
  transition: 0.5s;
}

footer .btns a:hover {
  background-color: #d1a42e;
  color: #fff;
  border-color: transparent;
}

footer .btns a:not(:last-child) {
  margin-left: 8px;
}

footer .end {
  margin-top: 80px;
  height: 50px;
  background-color: #f8f8f8;
}

@media (max-width: 767px) {
  footer .end {
    height: auto;
    padding: 10px 0;
  }
}

footer .end .container {
  height: 100%;
}

footer .end .content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

@media (max-width: 767px) {
  footer .end .content {
    flex-direction: column;
  }
}

footer .end p {
  font-size: 16px;
  font-family: med;
  color: #000;
  margin: 0;
}

@media (max-width: 480px) {
  footer .end p {
    text-align: center;
  }
}

footer .end p span {
  color: #d1a42e;
}

footer .end .socials {
  display: flex;
  align-items: center;
}

@media (max-width: 767px) {
  footer .end .socials {
    margin-top: 10px;
  }
}

footer .end .socials a {
  display: flex;
  align-items: center;
}

footer .end .socials a:not(:last-child) {
