}

.whats,
.live {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #009640;
  position: fixed;
  right: 24px;
  bottom: 62px;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.whats.show,
.live.show {
  opacity: 1;
  visibility: visible;
}

.live {
  background-color: #244d75;
  right: 74px;
}

.live .live-bg {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}
